Office.onReady(function (info) {
    if (info.host === Office.HostType.Outlook) {
        extractUrlFromEmail();
    }
});

const baseUrl = "http://www.centreblock.org/";

async function extractUrlFromEmail() {
    try {
        const item = Office.context.mailbox.item;

        item.body.getAsync("text", (result) => {
            if (result.status === Office.AsyncResultStatus.Succeeded) {
                const body = result.value;

                if (typeof body === "string") {
                    const urlMatch = body.match(/https?:\/\/[^\s]+/g);
                    if (urlMatch) {
                        const extractedUrl = urlMatch[0]; // Extract the first URL
                        const params = parseUrl(extractedUrl);
                        populateFormFields(params);
                        console.log("Extracted and populated URL:", extractedUrl);
                    } else {
                        console.log("No URL found in the email body.");
                    }
                } else {
                    console.error("Email body is not a string:", body);
                }
            } else {
                console.error("Error retrieving email body:", result.error);
            }
        });
    } catch (error) {
        console.error("Error extracting URL from email:", error);
    }
}

// Parse the URL and extract its parameters
function parseUrl(url) {
    try {
        const parsedUrl = new URL(url);
        const params = {};
        parsedUrl.searchParams.forEach((value, key) => {
            params[key] = value;
        });
        return params;
    } catch (error) {
        console.error("Error parsing URL:", error);
        return {};
    }
}

// Populate the form fields with URL parameters
function populateFormFields(params) {
    Object.entries(params).forEach(([key, value]) => {
        const inputField = document.getElementById(key);
        if (inputField) {
            inputField.value = decodeURIComponent(value); // Decode for user readability
        }
    });
}

// Parse and format multiple email addresses
function parseEmailAddresses(input) {
    return input
        .split(",") // Split by commas
        .map(email => email.trim()) // Remove spaces
        .filter(email => email.length > 0) // Exclude empty values
        .map(email => ({ emailAddress: email })); // Format for Office API
}

// Handle form submission
function handleSubmit(event) {
    event.preventDefault();

    const form = document.getElementById("urlParamsForm");
    const inputs = form.querySelectorAll("input");
    const updatedParams = [];

    // Collect updated form values
    inputs.forEach(input => {
        updatedParams.push(`${input.name}=${encodeURIComponent(input.value)}`);
    });

    const updatedUrl = `${baseUrl}?${updatedParams.join("&")}`;

    updateEmailFieldsAndBody(updatedUrl);
}

// Update email fields and body
function updateEmailFieldsAndBody(updatedUrl) {
    const item = Office.context.mailbox.item;

    // Update Subject, To, and Cc fields
    const toValue = document.getElementById("utm_campaign").value;
    const ccValue = document.getElementById("utm_term").value;
    const subjectValue = document.getElementById("utm_content").value;

    if (subjectValue) {
        item.subject.setAsync(subjectValue, (result) => {
            if (result.status === Office.AsyncResultStatus.Failed) {
                console.error("Error updating subject:", result.error.message);
            }
        });
    }

    if (toValue) {
        item.to.setAsync([toValue], (result) => {
            if (result.status === Office.AsyncResultStatus.Failed) {
                console.error("Error updating To field:", result.error.message);
            }
        });
    }

    if (ccValue) {
        // Split the CC value by commas and store as an array
        const ccRecipientsArray = ccValue.split(",").map(email => email.trim()).filter(email => email.length > 0);

        // Convert the array to the format expected by Office API
        const ccRecipients = ccRecipientsArray.map(email => ({ emailAddress: email }));

        // Update the CC field
        item.cc.setAsync(ccRecipients, (result) => {
            if (result.status === Office.AsyncResultStatus.Failed) {
                console.error("Error updating Cc field:", result.error.message);
            }
        });
    }


    // Update email body with the new URL
    item.body.getAsync("html", (result) => {
        if (result.status === Office.AsyncResultStatus.Succeeded) {
            let originalBody = result.value;

            // Remove existing URLs matching the base URL pattern
            const baseUrlPattern = /http:\/\/www\.centreblock\.org\/\?[^\s<]+/g;
            originalBody = originalBody.replace(baseUrlPattern, ""); // Remove old URLs

            // Append the new URL to the updated body
            const linkHTML = `<a href="${updatedUrl}" target="_blank">${baseUrl}</a>`;
            const updatedBody = `${originalBody}${linkHTML}`; // Append the new link

            item.body.setAsync(updatedBody, { coercionType: Office.CoercionType.Html }, (setResult) => {
                if (setResult.status === Office.AsyncResultStatus.Failed) {
                    console.error("Error updating email body:", setResult.error.message);
                }
            });
        } else {
            console.error("Error retrieving email body for update:", result.error.message);
        }
    });
}

